html, body {
	height: 100%;
}

#page {
	background: url(../img/paper-bg.png);
	min-height: 100%;
}

#wrapper-navbar {
	height: 168px;
	.mk-header {
		display: block;
		height: 229px;
		background: url(../img/sonne-wolken.png);
		background-repeat: no-repeat;
		background-position: top right;
		.navbar-toggler {
			margin-top: 1rem;
			left: 0.5rem;
			background: $primary;
			.navbar-toggler-icon {
				width: 1.5rem;
				height: 1.5rem;
			}
		}
		#context-menu {
			margin: 0.5rem 0 0;
			padding: 0;
			li {
				display: inline-block;
				font-family: 'Amatic SC', cursive;
				font-weight: bold;
				font-size: 1.8rem;
				line-height: 2rem;
				color: $montessori-hellgrau;
				a {
					color: $montessori-hellgrau;
					&:hover,
					&:focus {
						color: $montessori-rot;
					}
					&.nav-link {
						padding: 0;
						margin: 0;
					}
				}
			}
		}
		#context-menu a.current_page_item,
		#context-menu a.current-page-ancestor,
		.kinderhaus > #context-menu a[title="Kinderhaus"],
		.kinderdorf > #context-menu a[title="Kinderdorf"] {
			color: $montessori-dunkelgrau;
			&:first-letter {
				color: $montessori-rot;
				font-size: 2.2rem;
			}
		}
	}
	@include media-breakpoint-down(md) {
		height: auto;
		margin-bottom: 1.5rem;
		.mk-header {
			height: auto;
			padding: 0;
			background-size: 55%;
		}
	}

	@include media-breakpoint-down(sm) {
		.mk-header {
			background-size: 45%;
		}
	}
}

#left-sidebar {
	@extend .order-2;
	@extend .order-md-1;

	@include media-breakpoint-up(md) {
		.offcanvas, .offcanvas-start {
			display:block;
			visibility: visible !important;
			background: none;
			position: relative;
			transform: none;
			border: none;
			transition: none;
			top: initial;
			left: initial;
			width: auto;
			z-index: initial;
		}
	}
	#main-menu {
		li {
			display: block;
			float: none;
			line-height: 1.3;
			margin-bottom: 0.25rem;
			a {
				display: block;
				text-decoration: none;
				padding: 0.55rem 0.7rem;
				color: $montessori-grau;
				&:hover,
				&:focus {
					background: lighten($montessori-orange, 20%);
					text-decoration: none;
					color: white;
				}
			}
			hr {
				margin: 0.5rem 0;
				padding: 0;
				border-style: dotted;
				border-color: $montessori-orange;
				border-image-source: url(../img/src/dots.svg);
				border-image-slice: 33% 33%;
				border-image-repeat: round;
				border-width: 5px 0 0 0;
				background-color: transparent;
				opacity: 1;
			}
		}
		& > li.current-menu-item > a,
		.current-menu-ancestor .current-menu-item a {
			background: $montessori-orange;
			color: $white;
		}
		.sub-menu {
			display: none;
			margin-top: 0.25rem;
			padding-left: 1rem;
		}
		.current-menu-item, .current-menu-ancestor {
			.sub-menu {
				display: block;
			}
		}
	}

}

#primary {
	@extend .order-1;
	@extend .order-md-2;
}

#wrapper-footer-full {
	padding-bottom: 100px;
	background-color: transparent;

	footer.mk-footer {
		height: 67px;
		background: url(../img/src/wiese2.png);
		background-repeat: repeat-x;
		opacity: 0.9;
	}

	ul {
		padding: 0;
		margin: 0 0 0 2rem;
		position: fixed;
		bottom: 0.25rem;
		background: $white;
		font-size: 0.9rem;
		li {
			float: left;
			display: inline;
			width: auto;
			margin-left: 0.4rem;
			color: $montessori-grau;
			background: $white;
			@include border-radius;
			&:not(:last-child):after {
				content: "|";
				margin-left: 0.4rem;
			}
			&:last-child {
				margin-right: 0.4rem;
			}
			a.nav-link {
				display: inline;
				padding: 0;
				color: $montessori-grau;
			}
		}
	}
}

p {
	margin-bottom: .5rem;
}

.wrapper {
	padding: 0;
}

article.post {
	margin-bottom: 2rem;
}

.gallery-item {
	margin: 0 0 2rem;
	display: inline-block;
	padding: 1rem;
	background: $white;
	transform: rotate(-2deg);
	&:nth-child(2n) {
		transform: rotate(1.5deg);
	}
	&:nth-child(3n+1) {
		transform: rotate(.3deg);
	}
	&:nth-child(5n+3) {
		transform: rotate(-.7deg);
	}
	&:nth-child(7n+2) {
		transform: rotate(.4deg);
	}
	box-shadow: 3px 3px 10px #444;
	.wp-caption-text {
		@extend .h2;
		position: absolute;
		right: 0;
		bottom: 2rem;
		background: white;
		padding: 0.5rem 2rem;
		text-align: right;
	}
}
.foogallery-default.border-style-square-white {
	a, a:hover {
		box-shadow: 3px 3px 10px #444;
		transform: rotate(-2deg);
		&:nth-child(2n) {
			transform: rotate(1.5deg);
		}
		&:nth-child(3n+1) {
			transform: rotate(.3deg);
		}
		&:nth-child(5n+3) {
			transform: rotate(-.7deg);
		}
		&:nth-child(7n+2) {
			transform: rotate(.4deg);
		}
	}
}
.foogallery-default.spacing-width-10 a {
	margin-right: 1.5rem;
	margin-bottom: 1.5rem;
}

blockquote {
	margin: 2rem 0;
	padding: 0;
	font-family: 'Amatic SC', cursive;
	font-size: $blockquote-font-size;
	color: $montessori-orange;
	line-height: 1.2;
	p {
		padding: 0;
		margin: 0;
	}
	@include media-breakpoint-down(md) {
		font-size: 0.8 * $blockquote-font-size;
		margin: 1rem 0;
		span {
			font-size: 0.6 * $blockquote-font-size;
		}
	}
}

.wp-block-separator {
    border-top: 5px dotted !important;
    border-bottom: 0 !important;
}

hr {
	background-color: transparent;
	opacity: 1;
	margin: 1rem 0;
	padding: 0;
	border-style: dotted;
	border-color: $montessori-orange;
	border-image-source: url(../img/src/dots.svg);
	border-image-slice: 33% 33%;
	border-image-repeat: round;
	border-width: 5px 0 0 0;
}


h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	margin-bottom: $headings-margin-bottom;
	font-family: $headings-font-family;
	font-weight: $headings-font-weight;
	line-height: $headings-line-height;
	color: $headings-color;
	font-style: italic;
}
@include media-breakpoint-down(sm) {
	h1, .h1 {
		font-size: 1.6rem;
	}
	h2, .h2 {
		font-size: 1.5rem;
	}
	h3, .h3 {
		font-size: 1.4rem;
	}
	h4, .h4 {
		font-size: 1.3rem;
	}
	h5, .h5 {
		font-size: 1.2rem;
	}
	h6, .h6 {
		font-size: 1.1rem;
	}
}



.mk-butterfly {
	background: url(../img/schmetterling.png);
	width: 67px;
	height: 69px;
	position: fixed;
	right: 10px;
	@include media-breakpoint-down(sm) {
		display: none;
	}
}



.entry-meta {
	display: none;
}

.mk-calendar-toolbar {
	display: none;
}
.mk-agenda-view {
	margin-top: 2rem;
	.mk-date {
		margin-bottom: 3rem;
		.mk-date-title {
			width: 6rem;
			height: 6rem;
			font-size: 1.1rem;
			background: $montessori-orange;
			color: $white;
			border-radius: 50%;
			.mk-day-month {
				font-weight: bold;
				padding: 0.5rem 0 0;
				line-height: 3rem;
				height: 3rem;
			}
			.mk-year {
				border-top: 0.1rem solid $white;
				margin: 0 0.5rem;
				line-height: 2.5rem;
				height: 2.5rem;
			}
			@include media-breakpoint-down(sm) {
				display: inline-block;
				border-radius: 10rem;
				padding: 0 2rem;
				width: auto;
				height: auto;
				line-height: auto;
				margin: 0 0 0.5rem;
				.mk-day-month,
				.mk-year {
					display: inline-block;
					border: none;
					margin: 0;
					padding: 0;
				}

			}
		}
		.mk-date-range-spacer {
			color: $montessori-orange;
			width: 0rem;
			height: 3rem;
			margin-left: 2.5rem;
			overflow: hidden;
			border-style: dotted;
			border-color: $montessori-orange;
			border-image-source: url(../img/src/dots.svg);
			border-image-slice: 33% 33%;
			border-image-repeat: round;
			border-width: 0 10px 0 0;
			@include media-breakpoint-down(sm) {
				display: inline;
				width: auto;
				height: auto;
				margin-left: 0;
				border: 0;
			}
		}
		.mk-event {
			margin-top: 2rem;
			@include media-breakpoint-down(sm) {
				margin-top: 0;
			}
			.mk-event-agenda-picture {
				margin-bottom: 1rem;
				@include media-breakpoint-down(sm) {
					display: block;
					float: none;
					text-align: center;
				}
			}
			.mk-event-header {
				font-size: 1.7rem;
				.mk-event-time {
					font-size: 1.4rem;
					color: $montessori-grau;
				}
			}
		}
		.ai1ec-event-avatar {
			padding: 1rem;
			background: $white;
			box-shadow: 3px 3px 10px #444;
			transform: rotate(-2deg);
			@include media-breakpoint-down(md) {
				margin: 1rem;
			}
			img {
				min-height: auto;
				@extend .img-fluid;
			}
		}
		.btn, .btn-secondary {
			color: $montessori-rot;
			background: none;
			border: none;
			display: inline;
			outline: 0;
			margin: 0;
			padding: 0;
//			@include hover-focus {
//				color: $montessori-rot;
//				background: none;
//				text-decoration: underline;
//			}
		}
	}
}
article.ai1ec_event {
	.mk-single-event {
		.mk-actions {
			display: none;
		}
		.mk-event-details {
			color: $montessori-grau;
			margin-bottom: 1rem;
			font-size: 1.5rem;
			font-style: italic;
		}
	}
	.ai1ec-event-avatar {
		padding: 1rem;
		margin-bottom: 1rem;
		background: $white;
		box-shadow: 3px 3px 10px #444;
		transform: rotate(-2deg);
		@include media-breakpoint-down(md) {
			margin: 1rem;
			float: none;
			display: block;
		}
		img {
			max-width: auto;
			@extend .img-fluid;
		}
	}
	.ai1ec-allday-badge {
		display: none;
	}
}
.mk-hidden,
.mk-pagination {
	display: none;
}


p > img.size-medium,
.wp-block-image,
.wp-post-image {
	img {
		min-height: auto;
		@extend .img-fluid;
		padding: 1rem;
		margin-bottom: 1rem;
		background: $white;
		box-shadow: 3px 3px 10px #444;
		transform: rotate(-2deg);
		@include media-breakpoint-down(md) {
			margin: 1rem;
		}
	}
	figcaption {
		background: $montessori-rot;
		color: $white;
		margin-left: -1rem;
		padding-left: 1rem;
		margin-top: 1rem;
	}
}

