.foogallery-album-gallery-list {
	.foogallery-pile {
		.foogallery-pile-inner {
			text-align: center;
			background: $white;
			h3 {
				color: $montessori-rot !important;
				position: static;
				opacity: 0.8;
				font-size: 1.5rem;
			}
		}
	}
}

.sticky {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
}

.sticky:before,
.sticky:after {
	content: '';
	display: table;
}

.slider-pro {
	transform: rotate(-1deg);
	border-style: solid;
	background: white;
	border-color: white;
	border-width: 0.75rem 0.75rem 0px;
	box-shadow: 3px 3px 10px #444;
	margin: 2rem 0 !important;
	@include media-breakpoint-down(sm) {
		background: none;
		border: 0;
		transform: rotate(0);
		box-shadow: none;
	}
}

.sp-button {
	background: transparent !important;
	border: 0 !important;
	border-radius: 0;
}
.sp-selected-button {
	background: #97b8cd !important;
}

img.alignleft,
img.alignright {
	@include media-breakpoint-down(sm) {
		float: none;
		display: block;
	}
}
