// Use this file to overwrite the basic Bootstrap variables and add your own variables
// To overwrite a Bootstrap variable you don´t have to touch the Bootstrap folder.
// Just copy a variable from src/sass/assets/bootstrap5/_variables.scss, paste it here and edit the value.


$montessori-rot:			#d0031c;
$montessori-orange:			#d0801c;
$montessori-grau:			#7d7d7d;
$montessori-hellgrau:		#a0a0a0;
$montessori-dunkelgrau:		#575757;
$montessori-paper-bg:		#f3efea;

$primary:					$montessori-rot;
$link-hover-color:			$montessori-rot;
$body-bg:					#fff;

$font-family-serif:			'PT Serif', Georgia, "Times New Roman", Times, serif;
$font-family-sans-serif:	'Roboto', sans;
$font-family-base:			$font-family-serif;

$font-size-base:			1rem;
$font-size-lg:				1.5rem;
$font-size-sm:				1rem;
$font-size-xs:				.9rem;

$line-height-base:			1.7;

$font-size-h1:				2.0rem;
$font-size-h2:				1.8rem;
$font-size-h3:				1.6rem;
$font-size-h4:				1.4rem;
$font-size-h5:				1.2rem;
$font-size-h6:				1.1rem;
$headings-color:			$montessori-rot;
$headings-line-height:		1.3;

$blockquote-font-size:		($font-size-base * 2.5);

$navbar-padding-y:			0;
